.rightBarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.rightBarProfileImageContainer{
    margin-right: 10px;
    position: relative;
}
.rightBarProfileImage{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    object-fit: cover;
}
.rightBarOnline{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: limegreen;;
    position: absolute;
    top: -2px;
    right: 0;
    border: 2px solid white;
}
.rightBarUserName{
    font-weight: 500;
}